<template>
  <div>
    <Titletop title="个人中心">
      <router-link slot="l" replace to="/mineself"></router-link>
      <router-link slot="r" replace to="/index"></router-link>
    </Titletop>
    <PersonalSettings>
      <div class="password">
        <label for=""
          ><span>原密码</span><input type="text" v-model="oldpassword"
        /></label>
        <label for="">
          <span>新密码</span><input type="password" v-model="newPassworda"
        /></label>
        <label for=""
          ><span>确认密码</span><input type="password" v-model="newPasswordb"
        /></label>

        <button @click="submit">提交</button>
      </div>
    </PersonalSettings>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import PersonalSettings from "@/components/PersonalSettings.vue";
export default {
  name: "Mymypassword",
  props: ["gets"],
  components: { Titletop, PersonalSettings },
  data() {
    return {
      oldpassword: "",
      newPassworda: "",
      newPasswordb: "",
    };
  },
  mounted(){
    this.gets()
  },
  methods: {
    submit() {
      if (this.newPassworda !== this.newPasswordb) {
        this.$toast("两次密码不一致请重新输入");
        return;
      }
      this.axios.post(
        " https://jyapi.jyacg.com/admin/user/personupbase",
        {
          newPassworda: this.newPassworda,
          newPasswordb: this.newPasswordb,
          oldpassword: this.oldpassword,
          table: 3,
        },
        {
          headers: {
            authorization: localStorage.getItem("api_token"),
          },
        }
      ).then(res=>{
        console.log(res);
        if(res.status == 200){
            if(res.data.code == 0){
                this.$router.replace('/index')
                localStorage.removeItem("api_token")
                localStorage.removeItem("userapp")
            }else{
               this.$toast(res.data.msg)
            }
        }
      })
    },
  },
};
</script>

<style lang="scss" scoped>
.password {
  width: 275px;
  height: 211px;
  text-align: center;

  margin: 21px 25px 0px;
  label {
    display: block;
    margin-bottom: 15px;
    span {
      text-align: right;
      width: 60px;
      margin-right: 15px;
      display: inline-block;
      font-size: 15px;
      color: #333;
    }
  }
}
input {
  height: 25px;
  width: 180px;
  border-radius: 15px;
  border: 1px solid #333;
}
button {
  margin: 20px 0px;
  width: 118px;
  height: 30px;
  background-color: #439fff;
  color: #fff;
  border: none;
  border-radius: 15px;
}
</style>